import $http from "@/common/api/request.js"
import Lines from '@/components/common/Lines.vue'
import CommodityList from "@/components/common/CommodityList.vue"
export default{
	props:{
		keyword:String
	},
	data(){
		return{
			shopList:{
				currentIndex:0,
				data:[
					{name:'价格',status:1,key:"pprice"},
					{name:'折扣',status:0,key:"discount"},
					{name:'品牌',status:0}
				]
			},
			dataList:[]
		}
	},
	computed:{
		orderBy(){
			//拿到当前对象
			let obj = this.shopList.data[this.shopList.currentIndex];
			let val = obj.status === "1"?"desc":"asc";
			return{
				[obj.key]:val
			}
		}
	},
	components:{
		Lines,
		CommodityList
	},
	mounted() {
		this.getData()
	},
	methods:{
		//请求搜索数据
		getData(){
			$http.request({
				url:"/goods/search",
				data:{
					name:this.keyword,
					...this.orderBy
				}
			}).then((res)=>{
				this.dataList = res;
				// console.log(JSON.stringify(res))
			}).catch(()=>{
				uni.showToast({
					title:'请求失败',
					icon:'none'
				})
			})
		},
		changeTab(index){
			//点击排序===>重新请求数据
			this.getData()
			//索引值
			let idx = this.shopList.currentIndex

			//具体哪一个对象
			let item = this.shopList.data[idx]
			if( idx == index){
				return item.status = item.status === 1? 2:1
			}
			//新的值
			let newItem = this.shopList.data[index]
			item.status = 0
			this.shopList.currentIndex = index
			newItem.status = 1			
		}
	}
}
